import { Container, Typography, Card, CardContent, Stack } from '@mui/material';


function Info() {

    return (
        <Container sx={{
            display: 'flex',
            height: '100%',
            marginTop: '10px',
        }}>
            <Stack sx={{display: 'flex', flexDirection: 'column', justifyContent: 'space-between'}}>
                <Card>
                    <CardContent>
                        <Typography variant='h6'>   ChatGPT: </Typography>
                    </CardContent>
                </Card>
                <Card>
                    <CardContent>
                        <Typography>   ,  ,    ,  ,   .      .</Typography>
                    </CardContent>
                </Card>
            </Stack>
        </Container>
    );
}

export default Info;
