import { TOKEN } from './config.js';
const { Configuration, OpenAIApi } = require('openai');
const ozonTest = "Market Cap: 348,73 . ; Ebitda: -34,61 . ; P/E: 5,15; P/S: 1,4; Diluted EPS: 325,31 ; ROE: 337,33%: ROA: 32,26%";
const greeting = "  ,      ."


class APIOpenAI {
    constructor(token) {
        this.configuration = new Configuration({ apiKey: token });
        this.openai = new OpenAIApi(this.configuration);
    }

    async getRecommendation(text) {
        this.systemMessage = `   .   ,  ,   .           . `;

        if (!text) {
            const localAnswer = sessionStorage.getItem('answer');
            if (localAnswer) {
                return localAnswer;
            } else {
                text = greeting;
            }
        }

        this.response = await this.openai.createChatCompletion({
            model: 'gpt-3.5-turbo',
            messages: [
                { role: 'system', content: this.systemMessage },
                { role: 'user', content: text },
            ]
        });

        if (this.response.status === 200) {
            const answer = this.response.data.choices[0].message.content;
            if (answer) {
                sessionStorage.setItem('answer', answer)
                return answer;
            } else {
                return `    : ${answer}`;
            }
        } else {
            console.log('Response: ', this.response);
            return '  .   ,   .';
        }
    };
}

export default APIOpenAI;